import {
	getToken, setToken, removeToken
 } from '@/config/auth'
import { custLogin } from '@/apis/queueing'

const user = {
	state: {
		token: getToken()
	},
	mutations: {
		SET_TOKEN: (state, token) => {
			state.token = token
		}
	},
	actions: {
		// 用户名登录
		LoginByUsername({ commit }, userInfo) {
			return new Promise((resolve, reject) => {
				custLogin(userInfo).then(response => {
					if(response.result === 1) {
						// 存储 token
						commit('SET_TOKEN', response.model.token);
						setToken(response.model.token);
					}
					resolve(response)
				}).catch(error => {
					reject(error)
				})
			})
		},
		// 登出
		LogOut({ commit }) {
			return new Promise((resolve) => {
				commit('SET_TOKEN', '')
				removeToken()
				resolve()
			})
		}
	}
}

export default user
