import Vue from 'vue'
import axios from 'axios'
import router from '@/router'
import store from '@/store'
import {Toast,Dialog} from 'vant'
import { getIotToken } from '@/config/auth'
import { queueingBaseURL } from '@/config/const'

// 配置全局的axios
Vue.prototype.$https = axios;


let needLoadingRequestCount = 0
let loadingAxios; // 全局loading
export function showFullScreenLoading() {
	if (needLoadingRequestCount === 0) {
		// 创建loading
		loadingAxios = Toast.loading({
			duration: 0,       // 持续展示 toast
			mask: false,
			forbidClick: true,
			message: '加载中...'
		});
	}
	needLoadingRequestCount++
}

export function tryHideFullScreenLoading() {
	if (needLoadingRequestCount <= 0) return
	needLoadingRequestCount--
	if (needLoadingRequestCount === 0) {
		loadingAxios.clear(); // 关闭loading
	}
}

export function showMessageBox(code) {
	switch (code) {
		case 401:
			Dialog.alert({
				message: '登录失效，请重新登录',
				confirmButtonText: '重新登录'
			}).then(() => {
				store.dispatch('LogOut').then(() => {
					location.reload(); // 为了重新实例化vue-router对象 避免bug
				});
			});
			break;
		case 500:
			Toast('网络异常');
			break;
		case 504:
			Toast('网络异常');
			break;
		default:
			Toast('网络异常');
    }
}


const service = axios.create({
	baseURL: queueingBaseURL,
	timeout: 10000, // 请求超时时间设置
	withCredentials: false, //应许携带cookie
})
// 登录错误

// 添加请求拦截器
service.interceptors.request.use(
    (config) => {
        // 在发送请求之前做些什么
		if (store.getters.token) {
			config.headers['token'] = getIotToken() // 让每个请求携带token-- ['X-Token']为自定义key 请根据实际情况自行修改
		}
		showFullScreenLoading()
        return config;
    },
    (error) => {
        // 对请求错误做些什么
        return Promise.reject(error);
    }
)

// 添加响应拦截器
service.interceptors.response.use(
	(response) => {
		if (response.headers['x-authority-retoken']) { // 后台返回重置的token
			// setIotToken(response.headers['x-authority-retoken']);
		}
		tryHideFullScreenLoading();
		// 对响应数据做些什么
		return response;
	},
	(error) => {
		tryHideFullScreenLoading();
		// 对响应错误做些什么
		if (error.response) {
				if (error.response.status === 401) {
						// 401 未登陆 则需跳转登陆页
						router.replace({
								path: '/'
						})
				}
		}
		return Promise.reject(error.response.data);
	}
)

export default {
	//get请求
	get (url, params) {
		return new Promise((resolve, reject) => {
			url += (url.indexOf('?') < 0 ? '?' : '&') + param(params)
			service.get(url)
				.then(response => {

					if (response.status !== 200) {
						showMessageBox(response.status);
					} else {
						resolve(response.data);
					}
				}, err => {
					reject(err);
				})
				.catch((error) => {
					reject(error)
				})
		})
	},
	post (url, params, options) {
		console.log('xxx->', params)
    return new Promise((resolve, reject) => {
			service.post(url, params, options && options.urlencoded ? {
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				transformRequest:[
					function(data){
						return param(data); // 解决ajax问题
					}
				]
			} : {})
			.then(response => {
				console.log('response',response)
				if (response.status !== 200) {
					showMessageBox(response.status);
				} else {
					resolve(response.data);
				}
			}, err => {
				reject(err);
			})
			.catch((error) => {
				reject(error)
			})
		})
	},
	postFile (url, params, responseType) {
		return new Promise((resolve, reject) => {
			service.post(url, params, responseType)
				.then(response => {
					if (response.status !== 200) {
						showMessageBox(response.status);
					} else {
						resolve(response.data);
					}
				}, err => {
					reject(err);
				})
				.catch((error) => {
					reject(error)
				})
		})
	},
	postFileImport(url, params) {
		return new Promise((resolve, reject) => {
			service.post(url, params, {headers: {'Content-Type': 'multipart/form-data'}})
				.then(response => {
					if (response.status !== 200) {
						showMessageBox(response.status);
					} else {
						resolve(response.data);
					}
				}, err => {
					reject(err);
				})
				.catch((error) => {
					reject(error)
				})
		})
	},
	put(url, params) {
		return new Promise((resolve, reject) => {
			service.put(url, params)
				.then(response => {
					if (response.status !== 200) {
						showMessageBox(response.status);
					} else {
						resolve(response.data);
					}
				}, err => {
					reject(err);
				})
				.catch((error) => {
					reject(error)
				})
		})
	},
	delete(url, params) {
		url += (url.indexOf('?') < 0 ? '?' : '&') + param(params)
		return new Promise((resolve, reject) => {
			service.delete(url)
				.then(response => {
					if (response.status !== 200) {
						showMessageBox(response.status);
					} else {
						resolve(response.data);
					}
				}, err => {
					reject(err);
				})
				.catch((error) => {
					reject(error)
				})
		})
	}
}

/*
 * @param {a: 1, b: 222, c: 3}
 * @return "a=1&b=222&c=3"
 */
export function param(data) { // 拼接字符串
	let url = ''
	for (var k in data) {
		let value = data[k] !== undefined ? data[k] : ''
		url += '&' + k + '=' + encodeURIComponent(value)
	}
	return url ? url.substring(1) : ''  // 删掉第一个字母&
}
